<template>
  <div class="StatisticalAnalysis">
    <v-card>
      <v-card-title>
        住民分析
        <v-btn class="success mx-2" @click.stop="exportExcel"
          >轉出住民資料
          <v-icon class="ml-1">mdi-microsoft-excel</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-text-field
          label="月份設定"
          type="month"
          v-model="thisMonth"
          class="mx-1"
        ></v-text-field>
        <v-text-field
          label="年齡設定(起)"
          type="number"
          v-model="start_age"
          class="mx-1"
        ></v-text-field>
        <v-text-field
          label="年齡設定(迄)"
          type="number"
          v-model="end_age"
          class="mx-1"
        ></v-text-field>
        <v-btn class="primary mx-1" :loading="loading" @click="analysisData">
          <v-icon>mdi-google-analytics</v-icon>執行分析結果
        </v-btn>
      </v-card-title>

      <v-card-text>
        <v-simple-table class="text-no-wrap">
          <template v-slot:default>
            <thead>
              <tr>
                <th>日</th>
                <th v-for="(item, index) in analysisItems" :key="index">
                  {{ item.date }}
                </th>
                <th>總計</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>總人日數</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.count }}
                </td>
                <td>{{ total_counts("count") }}</td>
              </tr>
              <tr>
                <td>新住民數</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.new }}
                </td>
                <td>{{ total_counts("new") }}</td>
              </tr>
              <tr>
                <td>退住民數</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.liveOut }}
                </td>
                <td>{{ total_counts("liveOut") }}</td>
              </tr>
              <tr>
                <td>男.{{ start_age }}~{{ end_age }}.有殘冊</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.man65upHandicapYes }}
                </td>
                <td>{{ total_counts("man65upHandicapYes") }}</td>
              </tr>
              <tr>
                <td>男.{{ start_age }}~{{ end_age }}.無殘冊</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.man65upHandicapNo }}
                </td>
                <td>{{ total_counts("man65upHandicapNo") }}</td>
              </tr>
              <tr>
                <td>女.{{ start_age }}~{{ end_age }}.有殘冊</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.women65upHandicapYes }}
                </td>
                <td>{{ total_counts("women65upHandicapYes") }}</td>
              </tr>
              <tr>
                <td>女.{{ start_age }}~{{ end_age }}.無殘冊</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.women65upHandicapNo }}
                </td>
                <td>{{ total_counts("women65upHandicapNo") }}</td>
              </tr>
              <tr>
                <td>{{ start_age }}~{{ end_age }}.鼻胃管人數</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.pipelineNasogastricCounts }}
                </td>
                <td>{{ total_counts("pipelineNasogastricCounts") }}</td>
              </tr>
              <tr>
                <td>{{ start_age }}~{{ end_age }}.尿管人數</td>
                <td v-for="(item, index) in analysisItems" :key="index">
                  {{ item.pipelineUrinaryCounts }}
                </td>
                <td>{{ total_counts("pipelineUrinaryCounts") }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </v-card>

    <v-divider class="my-2"></v-divider>

    <Demography class="mb-6" />

    <!-- 住民輸出用表格 -->
    <table id="outputtable" border="2px solid #00" v-show="false">
      <thead class="font-weight-bold">
        <tr>
          <td>房號</td>
          <td>姓名</td>
          <td>身分證</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(user, index) in filterUserInLive" :key="index">
          <td>{{ user.bed_name }}</td>
          <td>{{ user.name }}</td>
          <td>{{ user.id_num }}</td>
        </tr>
      </tbody>
    </table>
    <!-- 住民輸出用表格 end-->

    <!-- <pre>
      userItems : {{userItems}}
    </pre> -->
  </div>
</template>

<script>
import XLSX from "xlsx";
import dayjs from "dayjs";
import Demography from "@/views/demography/Demography.vue";
import isBetween from "dayjs/plugin/isBetween";
dayjs.extend(isBetween);

export default {
  components: {
    Demography,
  },

  provide() {
    return {
      userItemsParents: () => this.userItems,
      bed_usedItemsParents: () => this.bed_usedItems,
    };
  },

  data() {
    return {
      temp: [],
      thisMonth: "",
      loading: false,

      start_age: 0,
      end_age: 65,

      userItems: [], //病歷資料
      bed_usedItems: [], //病床使用資料
      behospitalizedItems: [], //住院中資料
      pipeline_usedItems: [], //管路使用資料

      analysisItems: [],
    };
  },

  created() {
    this.getUser();
    // this.getBedUsed();
    this.getBehospitalized();

    let pipeline_usedapi =
      "general/getAll/" + this.$store.state.databaseName + "/pipeline_used";
    this.getItems(pipeline_usedapi, "pipeline_used"); //取得病床資料
  },

  computed: {
    //選出住宿中的病歷
    filterUserInLive() {
      let match = this.userItems.filter((i) => {
        return !!i.bed_name;
      });

      return match.sort((a, b) => {
        return a.bed_name > b.bed_name ? 1 : -1;
      });
    },
  },

  methods: {
    //分析所有資料及顯示
    analysisData() {
      this.loading = true;

      const lastDay = dayjs(this.thisMonth).endOf("month").format("DD");

      let analysisItems = [];
      for (var i = 1; i <= lastDay; i++) {
        // var count = this.getbed_used(i);//算出每日住民數

        analysisItems.push({
          date: i,
          count: this.getbed_used_day(i).length, //算出每日住民數
          new: this.getnew(i), //取得每日新住民數
          liveOut: this.getliveOut(i), //取得每日退住民數
          man65upHandicapYes: this.getUserClass(i, "男", "Yes"), //男.有殘冊
          man65upHandicapNo: this.getUserClass(i, "男", "No"), //男.無殘冊
          women65upHandicapYes: this.getUserClass(i, "女", "Yes"), //女.有殘冊
          women65upHandicapNo: this.getUserClass(i, "女", "No"), //女.無殘冊
          pipelineNasogastricCounts: this.getPipelindCounts(i, "鼻胃管").length, //鼻胃管人數
          pipelineUrinaryCounts: this.getPipelindCounts(i, "尿管").length, //尿管人數
        });
      }

      if (analysisItems.length) {
        this.analysisItems = analysisItems;
      } else {
        var pop = {
          msg: "無符合之資料!!請重新確認",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
      this.loading = false;
    },

    //依條件回傳資料  日.性別.有無殘冊
    getUserClass(day, sex, handicap) {
      var matchData = this.getbed_used_day(day);
      matchData = matchData.filter((item) => {
        // console.log(item)
        if (item.userData.birthday == undefined) {
          console.log(item);
        }
        var age =
          dayjs().format("YYYY") - dayjs(item.userData.birthday).format("YYYY");
        if (
          parseInt(age) >= this.start_age &&
          parseInt(age) <= this.end_age &&
          item.userData.sex == sex
        ) {
          if (handicap == "Yes") {
            if (item.userData.obstacle.length > 0) {
              return item;
            }
          } else {
            if (item.userData.obstacle == 0) {
              return item;
            }
          }
        }
      });
      return matchData.length;
    },
    //取得每日新住民數
    getnew(day) {
      const thisDay = dayjs(this.thisMonth).format("YYYY-MM-" + day);
      const matchData = this.bed_usedItems.filter((i) => {
        return (
          dayjs(thisDay).format("YYYY-MM-DD") ==
          dayjs(i.start_day).format("YYYY-MM-DD")
        );
      });
      return matchData.length;
    },
    //取得每日退住民數
    getliveOut(day) {
      const thisDay = dayjs(this.thisMonth).format("YYYY-MM-" + day);
      const matchData = this.bed_usedItems.filter((i) => {
        return (
          dayjs(thisDay).format("YYYY-MM-DD") ==
          dayjs(i.end_day).format("YYYY-MM-DD")
        );
      });
      return matchData.length;
    },
    //取得每日床位使用人數(不含H床),
    getbed_used_day(day) {
      const thisDay = dayjs(this.thisMonth).format("YYYY-MM-" + day);

      let matchData = this.bed_usedItems.filter((i) => {
        return (
          dayjs(thisDay).format("YYYY-MM-DD") >=
            dayjs(i.start_day).format("YYYY-MM-DD") &&
          (dayjs(thisDay).format("YYYY-MM-DD") <=
            dayjs(i.end_day).format("YYYY-MM-DD") ||
            i.end_day == "0000-00-00")
        );
      });

      // return matchData;
      //判斷是否住院中 -> 住院中就不算人日數

      // let count = 0;
      matchData = matchData.filter((i) => {
        let findData = this.behospitalizedItems.find((j) => {
          return i.u_snkey === j.u_snkey;
        });

        if (findData) {
          if (
            dayjs(thisDay).format("YYYY-MM-DD") >=
              dayjs(findData.start_day).format("YYYY-MM-DD") &&
            (dayjs(thisDay).format("YYYY-MM-DD") <=
              dayjs(findData.end_day).format("YYYY-MM-DD") ||
              findData.end_day == "0000-00-00")
          ) {
            // count++;
            // console.log(count,dayjs(thisDay).format("YYYY-MM-DD"),i.uname,"hospital", findData);
          } else {
            return i;
          }
        } else {
          return i;
        }
      });

      // console.log(dayjs(thisDay).format("YYYY-MM-DD"),matchData);
      return matchData;
    },
    //依key計算總數
    total_counts(key) {
      if (!!this.analysisItems) {
        return this.analysisItems.reduce((total, i) => {
          return parseInt(total) + parseInt(i[key]);
        }, 0);
      }
    },
    //取得所有住院資料
    getBehospitalized() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post(
          "bed_used/getAll/" +
            this.$store.state.databaseName +
            "/behospitalized"
        )
        .then((rs) => {
          this.behospitalizedItems = rs.data.map((i) => {
            var newItem = i;
            const matchItem = this.userItems.find((user) => {
              return user.snkey == i.u_snkey;
            });
            if (matchItem) {
              newItem.userData = matchItem;
            }
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        })
        .catch((err) => {
          console.log("no get");
        });
    },
    //取得所有入住資料
    getBedUsed() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料,不含H床
      this.axios
        .post("bed_used/getAll/" + this.$store.state.databaseName + "/bed_used")
        .then((rs) => {
          const matchData = rs.data.filter((i) => {
            return !i.bname.includes("H");
          });
          this.bed_usedItems = matchData.map((i) => {
            var newItem = i;
            const matchCounts = this.userItems.find((user) => {
              return user.snkey == i.u_snkey;
            });
            if (matchCounts) {
              newItem.userData = matchCounts;
            } else {
              // console.log(i);
              newItem.userData = "";
            }
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    //取得所有病歷資料
    getUser() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("user/getAll/" + this.$store.state.databaseName)
        .then((rs) => {
          this.userItems = rs.data;
          // .filter((i) => {
          //   return !!i.bed_name && !i.bed_name.includes("H") && i.hidden == "";
          // });
          this.getBedUsed();
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    //取得各項資料
    getItems(url, dataKey) {
      this.$api.options(url).then((rs) => {
        this[dataKey + "Items"] = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          newItem.u_snkey = i.u_snkey;
          return newItem;
        });
      });
    },
    //取得管路資料
    getPipelindCounts(day, type) {
      const thisDay = dayjs(this.thisMonth).format("YYYY-MM-" + day);
      if (this.pipeline_usedItems.length) {
        return this.pipeline_usedItems.filter((i) => {
          return (
            dayjs(thisDay).isBetween(
              i.start_date,
              dayjs(i.end_date),
              null,
              "[]"
            ) && i.name.includes(type)
          );
        });
      } else {
        var pop = {
          msg: "管路資料讀取失敗,請重新載入頁面",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },

    //轉出住民excel檔
    exportExcel() {
      this.$confirm("確認轉出住民Excel檔?").then((res) => {
        if (res) {
          let workbook = XLSX.utils.table_to_book(
            document.getElementById("outputtable")
          );
          let filename = `住民一覽表.xlsx`;
          try {
            // XLSX.writeFile(workbook, "成本估算單.xlsx");
            XLSX.writeFile(workbook, filename);
          } catch (e) {
            console.log(e, workbook);
          }
        }
      });
    },
  },
};
</script>


<style lang="scss">
