<template>
  <v-card class="age">
    <v-card-text>
      <v-simple-table class="text-no-wrap">
        <template v-slot:default>
          <thead>
            <tr>
              <th>月</th>
              <th v-for="item in 12" :key="item">{{ item }} 月</th>
              <th>合計</th>
              <th>年度平均</th>
              <!-- <th>
                <v-btn class="primary" @click.stop="createTableData"
                  >顯示資料</v-btn
                >
              </th> -->
            </tr>
          </thead>
          <tbody>
              <tr>
                  <td>總個案數</td>
                  <td v-for="item of tableData" :key="item.month">
                      {{ item.mansAll + item.womensAll }}
                  </td>
                  <td>
                     {{ total('mansAll') + total('womensAll') }}
                  </td>
                  <td>
                    {{ ((total('mansAll') + total('womensAll')) / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>59以下</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ item.mans59 + item.womens59 }}
                  </td>
                  <td>
                    {{ total('mans59') + total('womens59') }}
                  </td>
                  <td>
                    {{ ((total('mans59') + total('womens59')) / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>百分比</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ ((item.mans59 + item.womens59) / (item.mansAll + item.womensAll) * 100).toFixed(2) }}%
                  </td>
                  <td>
                    {{ ((total('mans59') + total('womens59')) / (total('mansAll') + total('womensAll')) * 100).toFixed(2) }}%
                  </td>
                  <td>
                    {{ ((((total('mans59') + total('womens59')) / 12).toFixed(0)) / (((total('mansAll') + total('womensAll')) / 12).toFixed(0)) * 100).toFixed(2) }}%
                  </td>
              </tr>
              <tr>
                  <td>61~69</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ item.mans60 + item.womens60 }}
                  </td>
                  <td>
                    {{ total('mans60') + total('womens60') }}
                  </td>
                  <td>
                    {{ ((total('mans60') + total('womens60')) / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>百分比</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ ((item.mans60 + item.womens60) / (item.mansAll + item.womensAll) * 100).toFixed(2) }}%
                  </td>
                  <td>
                    {{ ((total('mans60') + total('womens60')) / (total('mansAll') + total('womensAll')) * 100).toFixed(2) }}%
                    
                  </td>
                  <td>
                    {{ ((((total('mans60') + total('womens60')) / 12).toFixed(0)) / (((total('mansAll') + total('womensAll')) / 12).toFixed(0)) * 100).toFixed(2) }}%

                  </td>
              </tr>
              <tr>
                  <td>70~79</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ item.mans70 + item.womens70 }}
                  </td>
                  <td>
                    {{ total('mans70') + total('womens70') }}
                  </td>
                  <td>
                    {{ ((total('mans70') + total('womens70')) / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>百分比</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ ((item.mans70 + item.womens70) / (item.mansAll + item.womensAll) * 100).toFixed(2) }}%
                  </td>
                  <td>
                    {{ ((total('mans70') + total('womens70')) / (total('mansAll') + total('womensAll')) * 100).toFixed(2) }}%
                    
                  </td>
                  <td>
                    {{ ((((total('mans70') + total('womens70')) / 12).toFixed(0)) / (((total('mansAll') + total('womensAll')) / 12).toFixed(0)) * 100).toFixed(2) }}%

                  </td>
              </tr>
              <tr>
                  <td>80~89</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ item.mans80 + item.womens80 }}
                  </td>
                  <td>
                    {{ total('mans80') + total('womens80') }}
                  </td>
                  <td>
                    {{ ((total('mans80') + total('womens80')) / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>百分比</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ ((item.mans80 + item.womens80) / (item.mansAll + item.womensAll) * 100).toFixed(2) }}%
                  </td>
                  <td>
                    {{ ((total('mans80') + total('womens80')) / (total('mansAll') + total('womensAll')) * 100).toFixed(2) }}%
                    
                  </td>
                  <td>
                    {{ ((((total('mans80') + total('womens80')) / 12).toFixed(0)) / (((total('mansAll') + total('womensAll')) / 12).toFixed(0)) * 100).toFixed(2) }}%

                  </td>
              </tr>
              <tr>
                  <td>90以上</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ item.mans90 + item.womens90 }}
                  </td>
                  <td>
                    {{ total('mans90') + total('womens90') }}
                  </td>
                  <td>
                    {{ ((total('mans90') + total('womens90')) / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>百分比</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ ((item.mans90 + item.womens90) / (item.mansAll + item.womensAll) * 100).toFixed(2) }}%
                  </td>
                  <td>
                    {{ ((total('mans90') + total('womens90')) / (total('mansAll') + total('womensAll')) * 100).toFixed(2) }}%
                    
                  </td>
                  <td>
                    {{ ((((total('mans90') + total('womens90')) / 12).toFixed(0)) / (((total('mansAll') + total('womensAll')) / 12).toFixed(0)) * 100).toFixed(2) }}%

                  </td>
              </tr>
              
          </tbody>
        </template>
      </v-simple-table>
    </v-card-text>

    <!-- <pre>
        {{ bed_usedItems }}
    </pre> -->

  </v-card>
</template>

<script>
import dayjs from "dayjs";

export default {
  props: ["year"],
  inject: ["userItemsParents", "bed_usedItemsParents"],

  data() {
    return {
      tableData: [],
    };
  },

  watch:{
      year(){
          this.createTableData();
      },
      bed_usedItems(){
          this.createTableData();
      }
  },

  computed: {
    //常態性更新從祖父端讀進來的值
    userItems() {
      return this.userItemsParents();
    },
    bed_usedItems() {
      return this.bed_usedItemsParents();
    },
    
  },

  filters:{
      filterEmpty(val){
          if (val == 'NaN'){
              return ''
          }else{
              return val;
          }
      }
  },

  mounted(){
    this.createTableData();
  },

  methods: {
    //創建表格資料
    createTableData() {
      let tableData = [];

      for (let month = 1; month <= 12; month++) {
          tableData.push({
            month: month,  
            mansAll: this.getClass(month,'男',0,300).length,
            womensAll: this.getClass(month,'女',0,300).length,
            mans59: this.getClass(month,'男',0,59).length,
            womens59: this.getClass(month,'女',0,59).length,
            mans60: this.getClass(month,'男',60,69).length,
            womens60: this.getClass(month,'女',60,69).length,
            mans70: this.getClass(month,'男',70,79).length,
            womens70: this.getClass(month,'女',70,79).length,
            mans80: this.getClass(month,'男',80,89).length,
            womens80: this.getClass(month,'女',80,89).length,
            mans90: this.getClass(month,'男',90,300).length,
            womens90: this.getClass(month,'女',90,300).length,
          });
      }

      this.tableData = tableData;

    },
    //依性別取得相符的資料
    getClass(month,sex,start_age,end_age){
        const thisMonthStart = dayjs(this.year+'-'+month).format("YYYY-MM-01");
        const thisMonthEnd = dayjs(this.year+'-'+month).endOf('month').format("YYYY-MM-DD");

        return this.bed_usedItems.filter(i=>{
            let age = dayjs().format("YYYY") - dayjs(i.userData.birthday).format("YYYY");
            // console.log(i.userData.name,age)
            if (
                i.start_day <= thisMonthEnd
             && i.end_day <= thisMonthEnd
             && (
                    i.end_day >= thisMonthStart
                ||  i.end_day == '0000-00-00'
                )
             && i.userData.sex == sex
             && parseInt(age) >= parseInt(start_age) && parseInt(age) <= parseInt(end_age) 
            ){
                return i;
            }    
        });
        
    },
    //總計
    total(type){
      return this.tableData.reduce((total,element)=>{
        return total += parseInt(element[type])
      },0);  
    }
  },
};
</script> 

<style scoped>
.theme--light.v-data-table thead tr th {
  font-size: 1rem;
}
</style>