<template>
  <v-card class="sex">
    <v-card-text>
      <v-simple-table class="text-no-wrap">
        <template v-slot:default>
          <thead>
            <tr>
              <th>月</th>
              <th v-for="item in 12" :key="item">{{ item }} 月</th>
              <th>合計</th>
              <th>年度平均</th>
              <!-- <th>
                <v-btn class="primary" @click.stop="createTableData"
                  >顯示資料</v-btn
                >
              </th> -->
            </tr>
          </thead>
          <tbody>
              <tr>
                  <td>男性</td>
                  <td v-for="item of tableData" :key="item.month">
                      {{ item.mans }}
                  </td>
                  <td>
                    {{ total('mans') }}
                  </td>
                  <td>
                    {{ (total('mans') / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>女性</td>
                  <td v-for="item of tableData" :key="item.month">
                      {{ item.womens }}
                  </td>
                  <td>
                    {{ total('womens') }}
                  </td>
                  <td>
                    {{ (total('womens') / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>合計</td>
                  <td v-for="item of tableData" :key="item.month">
                      {{ item.mans + item.womens }}
                  </td>
                  <td>
                    {{ total('mans') + total('womens') }}
                  </td>
                  <td>
                    {{ ((total('mans') + total('womens'))/12).toFixed(0)}}
                  </td>
              </tr>
              <tr>
                  <td>男生百分比</td>
                  <td v-for="item of tableData" :key="item.month">
                      {{ ((item.mans / (item.mans + item.womens))*100).toFixed(2) | filterEmpty }} %
                  </td>
                  <td>
                    {{ ((total('mans') / (total('mans') + total('womens')))*100).toFixed(2) | filterEmpty }} %
                  </td>
                  <td>
                    
                  </td>
              </tr>
              <tr>
                  <td>女生百分比</td>
                  <td v-for="item of tableData" :key="item.month">
                      {{ ((item.womens / (item.mans + item.womens))*100).toFixed(2) | filterEmpty }} %
                  </td>
                  <td>
                    {{ ((total('womens') / (total('mans') + total('womens')))*100).toFixed(2) | filterEmpty }} %
                  </td>
                  <td>
                    
                  </td>
              </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-card-text>

    <!-- <pre>
        {{ tableData }}
    </pre> -->

  </v-card>
</template>

<script>
import dayjs from "dayjs";

export default {
  props: ["year"],
  inject: ["userItemsParents", "bed_usedItemsParents"],

  data() {
    return {
      tableData: [],
    };
  },

  watch:{
      year(){
          this.createTableData();
      },
      bed_usedItems(){
          this.createTableData();
      }
  },

  computed: {
    //常態性更新從祖父端讀進來的值
    userItems() {
      return this.userItemsParents();
    },
    bed_usedItems() {
      return this.bed_usedItemsParents();
    },
    
  },

  filters:{
      filterEmpty(val){
          if (val == 'NaN'){
              return ''
          }else{
              return val;
          }
      }
  },

  mounted(){
    this.createTableData();
  },

  methods: {
    //創建表格資料
    createTableData() {
      let tableData = [];

      for (let month = 1; month <= 12; month++) {
          tableData.push({
            month: month,  
            mans: this.getSex(month,'男').length,
            womens: this.getSex(month,'女').length,
          });
      }

      this.tableData = tableData;

    },
    //依性別取得相符的資料
    getSex(month,sex){
        const thisMonthStart = dayjs(this.year+'-'+month).format("YYYY-MM-01");
        const thisMonthEnd = dayjs(this.year+'-'+month).endOf('month').format("YYYY-MM-DD");

        return this.bed_usedItems.filter(i=>{
            if (
                i.start_day <= thisMonthEnd
             && i.end_day <= thisMonthEnd
             && (
                    i.end_day >= thisMonthStart
                ||  i.end_day == '0000-00-00'
                )
             && i.userData.sex == sex
            ){
                return i;
            }    
        });
    },
    //總計
    total(type){
      return this.tableData.reduce((total,element)=>{
        return total += parseInt(element[type])
      },0);  
    }
  },
};
</script> 

<style scoped>
.theme--light.v-data-table thead tr th {
  font-size: 1rem;
}
</style>