<template>
  <v-card class="bed">
    <v-card-text>
      <v-simple-table class="text-no-wrap">
        <template v-slot:default>
          <thead>
            <tr>
              <th>月</th>
              <th v-for="item in 12" :key="item">{{ item }} 月</th>
              <th>合計</th>
              <th>年度平均</th>
              <!-- <th>
                <v-btn class="primary" @click.stop="createTableData"
                  >顯示資料</v-btn
                >
              </th> -->
            </tr>
          </thead>
          <tbody>
              <tr>
                  <td>人數</td>
                  <td v-for="item of tableData" :key="item.month">
                      {{ item.mans + item.womens }}
                  </td>
                  <td>
                     {{ total('mans') + total('womens') }}
                  </td>
                  <td>
                    {{ ((total('mans') + total('womens')) / 12).toFixed(0) }}
                  </td>
              </tr>
              <tr>
                  <td>床數</td>
                  <td v-for="item of tableData" :key="item.month">
                    {{ bedItems.length }}
                  </td>
                  <td>
                    {{ bedItems.length * 12 }}
                  </td>
                  <td>
                    {{ bedItems.length * 12 / 12 }}
                  </td>
              </tr>
              <tr>
                  <td>占床率</td>
                  <td v-for="item of tableData" :key="item.month">
                      {{ ((item.mans + item.womens) / bedItems.length * 100 ).toFixed(2) }}%
                  </td>
                  <td>
                    
                  </td>
                  <td>
                    {{ (((total('mans') + total('womens')) / 12).toFixed(0) / (bedItems.length * 12 / 12) * 100).toFixed(2) }}%
                  </td>
              </tr>
              
          </tbody>
        </template>
      </v-simple-table>
    </v-card-text>

    <!-- <pre>
        {{ bedItems }}
    </pre> -->

  </v-card>
</template>

<script>
import dayjs from "dayjs";

export default {
  props: ["year"],
  inject: ["userItemsParents", "bed_usedItemsParents"],

  data() {
    return {
      tableData: [],
      bedItems:[],
    };
  },

  watch:{
      year(){
          this.createTableData();
      },
      bed_usedItems(){
          this.createTableData();
      }
  },

  computed: {
    //常態性更新從祖父端讀進來的值
    userItems() {
      return this.userItemsParents();
    },
    bed_usedItems() {
      return this.bed_usedItemsParents();
    },
    
  },

  filters:{
      filterEmpty(val){
          if (val == 'NaN'){
              return ''
          }else{
              return val;
          }
      }
  },

  mounted(){
    this.getData('bed');
    this.createTableData();
  },

  methods: {
    getData(tableName){
      this.$api.options("bed/getAll/" + this.$store.state.databaseName + "/" + tableName)
      .then((rs)=>{
        this[tableName + 'Items'] = rs;
      })
    },
    //創建表格資料
    createTableData() {
      let tableData = [];

      for (let month = 1; month <= 12; month++) {
          tableData.push({
            month: month,  
            mans: this.getSex(month,'男').length,
            womens: this.getSex(month,'女').length,
          });
      }

      this.tableData = tableData;

    },
    //依性別取得相符的資料
    getSex(month,sex){
        const thisMonthStart = dayjs(this.year+'-'+month).format("YYYY-MM-01");
        const thisMonthEnd = dayjs(this.year+'-'+month).endOf('month').format("YYYY-MM-DD");

        return this.bed_usedItems.filter(i=>{
            if (
                i.start_day <= thisMonthEnd
             && i.end_day <= thisMonthEnd
             && (
                    i.end_day >= thisMonthStart
                ||  i.end_day == '0000-00-00'
                )
             && i.userData.sex == sex
            ){
                return i;
            }    
        });
    },
    //總計
    total(type){
      return this.tableData.reduce((total,element)=>{
        return total += parseInt(element[type])
      },0);  
    }
  },
};
</script> 

<style scoped>
.theme--light.v-data-table thead tr th {
  font-size: 1rem;
}
</style>